.border {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    position: relative;
    width: 64px;
    height: 64px;
    background: linear-gradient(to right, #801AB2, #4C4CFF);
}


.image {
    border-radius: 50%;
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    object-fit: cover;
    object-position: center;
    background: #131316;
}

.extra {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 2px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    border-radius: 50%;
    background-color: rgba(28, 28, 33, 0.6);
}